import { groupBy, mapValues, reduce } from "lodash"

const data = mapValues({
  牛奶盒: require('./牛奶盒').default,
  珍奶杯: require('./珍奶杯').default,
  咖啡杯: require('./咖啡杯').default,
}, imgs => {
  const ordered = imgs.map((cfg, index) => ({ ...cfg, index, order: typeof cfg.order === 'number' ? cfg.order : index }))
  const positions = []
  const totalHeight = reduce(groupBy(ordered, 'order'), (total, orderEle, order) => {
    const y = total + orderEle[0].height
    positions[order] = y
    return y
  }, 0)
  return {
    totalHeight,
    positions,
    imgs: ordered,
  }
})

export default data
